import Vue from 'vue'
import Router from 'vue-router'


import Home from '../pages/home/home.vue'
import Foods from '../pages/foods/foods.vue'
import Travel from '../pages/travel/travel.vue'
import My from '../pages/My/my.vue'

import Intro from '../pages/travel/introduce.vue'
import Leave from '../pages/travel/leave.vue'

import Food from '../pages/foods/food.vue'
import Cook from '../pages/foods/cook.vue'
import Mothers from '../pages/foods/mothers.vue'
import Health from '../pages/foods/mothers.vue'
import CateItem from '../pages/foods/cateItem.vue'

import Audier from '../pages/My/audier.vue'
import Radio from '../pages/My/radio.vue'
import Me from '../pages/My/me.vue'

import tMap from '../pages/home/aMap.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/home',
      component: Home
    },
     {
      path: '/foods',
      component: Foods
    },
     {
      path: '/travel',
      component: Travel,
      children:[
          {path:'intro',component:Intro},
          {path:'leave',component:Leave}
      ]
    },
     {
      path: '/My',
      component: My
    },
     {
      path: '/Food',
      component: Food
    },
     {
      path: '/Cook',
      component: Cook
    },
     {
      path: '/Mothers',
      component: Mothers
    },
    {
      path: '/Health',
      component: Health
    },
     {
      path: '/Audier',
      component: Audier
    },
     {
      path: '/Radio',
      component: Radio
    },
    {
      path: '/Me',
      component: Me
    },
    {
      path: '/cateItem',
      component: CateItem
    },
    {
      path: '/aMap',
      component: tMap
    }
    
  ]
})



