<template>
	<div class='itmewrap' @click='fn'>
		<span v-show='bol'><slot name=cotimg1></slot></span>
		<span v-show='!bol'><slot name=cotimg2></slot></span>
		<p :class='{green:!bol}'>{{ txtcot }}</p>
	</div>
</template>

<script>
	export default{

		props:['txtcot','mark','selecte'],

		methods:{
			fn:function(){
				this.$emit('change',this.mark)
				this.$router.push('/'+this.mark)
			}
		},
		computed:{//
			bol:function(){
				if (this.mark == this.selecte){
				return false;
				}				
				return true	;
			}
		}
	}

</script>

<style>
	.itmewrap{width:25%;float:left;}
	.itmewrap img{width:0.8rem;}
	.itmewrap p{font-size:0.28rem;line-height: 0.1rem;}
	.itmewrap .green{color: #42BD56;}
</style>


