<template>
<div>
	<div class='itme'>
		<itme txtcot='' mark='home' :selecte='selected' @change='change'>
			<img src="../assets/images/ic_tab_home_normal.png" slot='cotimg1'/>
			<img src="../assets/images/ic_tab_home_active.png" slot='cotimg2'/>
		</itme>

		<itme txtcot='' mark='foods' :selecte='selected' @change='change'>
			<img src="../assets/images/ic_tab_group_normal.png" slot='cotimg1'/>
			<img src="../assets/images/ic_tab_group_active.png" slot='cotimg2'/>
		</itme>

		<itme txtcot='' mark='travel' :selecte='selected' @change='change'>
			<img src="../assets/images/ic_tab_status_normal.png" slot='cotimg1'/>
			<img src="../assets/images/ic_tab_status_active.png" slot='cotimg2'/>
		</itme>

		<itme txtcot='' mark='My' :selecte='selected' @change='change'>
			<img src="../assets/images/ic_tab_profile_female_normal.png" slot='cotimg1'/>
			<img src="../assets/images/ic_tab_profile_female_active.png" slot='cotimg2'/>
		</itme>

	</div>
</div>
</template>

<script>
	import itme from './itme'
	export default {
		data:function(){
			return {
				selected: 'We'
			}
		},
		components:{
			itme
		},
		methods:{
			change:function(val){
				this.selected = val;	
			}
		}
	}
</script>

<style>
	.itme{width:100%;height:1.2rem;position: fixed;left: 0;bottom: 0;border-top:0.01rem solid #ccc;padding-top: 0.05rem;background: #f7f7f7;text-align: center;background-color: #fff;z-index: 999;}
</style>


