<template>
	<div class="swiper-container" :class='swipeid'>
		<div class="swiper-wrapper">
			<slot name='swiper-con'></slot>
		</div>
	</div>
</template>

<script>
	//swiperjs
	import '../assets/swiper.min.js'         
	export default {
		props:{
			swipeid:{   //swiper
				type:String,
				default:''
			},
			effect:{    //
				type:String,
				default:'slide'
			},
			loop:{		//
				type:Boolean,
				default:true
			},
			direction:{  //
				type:String,
				default:'horizontal'    //
			},
			autoplay:{   //
				type:Number,
				default:5000
			}
		},
		mounted:function(){
				var that = this;
				new Swiper('.'+that.swipeid,{
					loop:that.loop,
					pagination:'.swiper-pagination',
					paginationType:that.paginationType,
					autoplay:that.autoplay,
					direction:that.direction,
					effect:that.effect,
					//swiperautoplay
					autoplayDisableOnInteraction:false
				});
			}
	}
</script>

<style>
	/*swipercss,*/
	@import '../assets/swiper.min.css';
	.swiper-container img{width: 100%;}
</style>


