<template>
	<div class="foodsWrap">
		<myBanner swipeid='swipe01' :autoplay='1000'>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner3.png"/>
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner4.png"/>
			</div>
		</myBanner>
		<ul class="clearfix">
			<li>
				<router-link to='/Food'>
					<img src="../../assets/images/ms.jpg"/><br/>
					<span></span>
				</router-link>
			</li>
			<li>
				<router-link to='/Cook'>
					<img src="../../assets/images/cy.jpg"/><br/>
					<span></span>
				</router-link>
			</li>
			<li>
				<router-link to='/Mothers'>
					<img src="../../assets/images/mmp.jpg"/><br/>
					<span></span>
				</router-link>
			</li>
			<li>
				<router-link to='/Health'>
					<img src="../../assets/images/ys.jpg"/><br/>
					<span></span>
				</router-link>
			</li>
		</ul>
		<h2></h2>
		<Cate :h='h1' :p='p1' :time='time1'>
			<img src="../../assets/images/detail.png" class='pic'/>		
		</Cate>
		<Cate :h='h2' :p='p2' :time='time2'>
			<img src="../../assets/images/detail.png" class='pic'/>	
		</Cate>
		<Cate :h='h3' :p='p3' :time='time3'>
			<img src="../../assets/images/detail.png" class='pic'/>
		</Cate>
	</div>
</template>

<script>
	import myBanner from '../../components/banner'
	import Cate from './cate'
	export default {
		components:{
			myBanner,
			Cate
		},
		data () {
			return {
				nowIndex:1,
				h1:'',
				p1:'5',
				time1:'2017-5-23',
				h2:'',
				p2:'5',
				time2:'2017-5-23',
				h3:'',
				p3:'5',
				time3:'2017-5-23',
			}
		}
	}
</script>

<style>
	*{margin:0;padding: 0;}
	.theme{background: #f7f7f7;margin-top: -0.05rem;}
	.foodsWrap h2{font-size: 0.35rem;text-align: left;padding-left: 0.2rem;height: 0.8rem;line-height: 0.8rem;}
	.foodsWrap ul{text-align: center;}
	.foodsWrap li{float: left;width: 25%;list-style: none;}
	.foodsWrap li a{font-size: 0.2rem;color: #666;text-decoration: none;display: inline-block;}
	.foodsWrap li img{width: 0.6rem;}
	.foodsWrap li span{line-height: 0.15rem;}
	.foodsWrap .active{background: ##FF7373}
	.foodsWrap .active a{color: ##FF7373}
	.foodsWrap .pic{width: 1.5rem;float: left;padding-top: 0.2rem;padding-right: 0.15rem;}

</style>


