<template>
	<div class="cateWrap">
		<router-link to='/cateItem'>
			<slot></slot>
			<h3>{{h}}</h3>
			<p>{{p}}</p>
			<div class="time">
				<time>{{time}}</time>
				<span><i><img src="../../assets/images/icon2.jpg"/></i></span>
			</div>	
		</router-link>	
	</div>
</template>

<script>
	export default {
		props:['h','p','time']
	}
</script>

<style>
	.cateWrap{border-top:0.01rem solid #ccc;height: 2rem; padding: 0 0.1rem;}
	.cateWrap h3{text-align: left;padding-top: 0.4rem;font-weight: normal;color: #333;font-size: 0.33rem;}
	.cateWrap p{text-align: left;color: #666;font-size: 0.28rem;}
	.cateWrap .time{font-size: 0.25rem;color: #666;padding-top: 0.05rem;text-align: left;}
	.cateWrap .time span{float: right;}
	.cateWrap i{font-style: normal;}
	.cateWrap i img{width: 0.45rem;}
	.cateWrap a{text-decoration: none;}
</style>


