<template id="myheader">
	<div id="headerWrap">
		<span>{{txt}}</span>
		<slot name='left'></slot>
		<slot name='right'></slot>
		<slot name='searchInput'></slot>
		<slot name='search'></slot>
		<slot name='scan'></slot>
	</div>
</template>

<script>
	export default {
		props:['txt']
	}
</script>

<style>
	*{margin: 0;padding: 0;box-sizing:border-box;}
	#headerWrap{width: 100%;height: 0.9rem;line-height: 0.9rem;background: #eee;text-align: center;font-size: 0.22rem;font-weight: bold;}
	#headerWrap img{width: 0.6rem;position: absolute;top: 0.20rem;}
	#headerWrap .left{right: 1rem;}
	#headerWrap .right{right: 0.15rem;}
</style>


