<template>
	<div class="homeWrap">
		<Myheader class='myHeader'>
			<img src="../../assets/images/ic_chat_white.png" alt="" class="right" slot="right">
			<img src="../../assets/images/ic_search_gray.png" alt="" id="search" slot='search'>
			<img src="../../assets/images/ic_scan_gray.png" alt="" id="scan" slot='scan'>
			<input slot='searchInput' :placeholder="msg" @click='fn1()'></input>
		</Myheader>
		<myBanner swipeid='swipe01' :autoplay='2000' class='myBanner'>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner_cxl.jpg">
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner_ifs.jpg"/>
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner_tgl_over.jpg"/>
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner_tgl_1.jpg"/>
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner_lqyj.jpg">
			</div>

		</myBanner>
	
		<Introduce class='introduce'></Introduce>
		
		<Rooms></Rooms>
		
		<div class="location">
			<div class="title clearfix"><span></span></div>
			<div class="map">
				<div class="txt">
					<h5></h5>
					<p>622E1/E250</p>
				</div>
				<router-link to='/aMap' class='pic'><img src="../../assets/images/sdhzslt.jpg"></router-link>
			</div>
			<span class="tel">:15208293728</span>
		</div>


	</div>	
</template>

<script type="text/javascript">
	import Myheader from './header.vue'
	import myBanner from '../../components/banner'
	import Introduce from './introduce.vue'
	import Rooms from './room.vue'
	export default{
			components:{
				Myheader,
				myBanner,
				Introduce,
				Rooms			
			},
			data:function(){
				return{
					msg:''
				}
			},
			methods:{
				fn1:function(){
					this.msg=''
					var that = this;
					setTimeout(function(){
						that.msg=''
					},2000)
					
				}
			}

		}	
</script>

<style type="text/css">
	.homeWrap #headerWrap{background: #42BD56;}
	.homeWrap .myHeader{position: fixed;top: 0;z-index: 3;}
	input{height: 0.6rem;width: 80%;border-radius:0.05rem;border:none;background: #fff;font-size: 0.22rem;position: absolute;left: 0.2rem;top:0.15rem;text-indent: 2em;}
	#headerWrap #search{width: 0.4rem;position: absolute;left: 0.3rem;top: 0.3rem;z-index: 2}
	#headerWrap #scan{width: 0.4rem;position: absolute;right:1.4rem;top:0.27rem;z-index: 2}
	.myBanner{padding-top: 1rem;}
	.location{padding-bottom: 2rem;}
	.location .title{width: 100%;height: 1.6rem;background: url(../../assets/images/ic_rec_group_banner_5.png);background-size:cover;position: relative; }
	.location .title span{position: absolute;left: 0.4rem;top: 0.5rem;color: #fff;font-size: 0.4rem;}
	.location .map{display: flex;padding:0.2rem;}
	.location .txt{width: 100%;padding-right: 0.2rem;}
	.location .txt h5{font-weight: normal;height: 0.5rem;line-height: 0.5rem;}
	.location .txt p{text-indent: 2em;font-size: 0.27rem;}
	.location .map .pic{background-image:-webkit-linear-gradient(to right,rgba(255,255,255,0) 0%,rgba(255,255,255,0.4) 5%,rgba(255,255,255,0.6) 10%,rgba(255,255,255,0.6) 90%,rgba(255,255,255,0.4) 95%,rgba(255,255,255,0) 100%);}
	.location img{width: 100%;}
	.location .tel{font-size: 0.4rem;padding-left: 0.5rem;}
	.amap{border: 0.02rem solid #ccc;padding-bottom: 10rem;}
</style>



