<template>
	<div class="tabcot">
		<div class="oTab">
			<span :class='{gaer:setl==1}' @click='setl=1'>
				<router-link to='/travel/intro'></router-link>
			</span>

			<span :class='{gaer:setl==2}' @click='setl=2'>
				<router-link to='/travel/leave'></router-link>
			</span>	
		</div>
		<router-view></router-view>
	</div>
</template>
<script type="text/javascript">
	export default{
		data:function(){
			return{
				setl:1
			}
		}
	}
</script>
<style type="text/css">
	.oTab {width: 95%;height: 60px;margin-left: 5%;}
	.oTab span{width: 28%;height: 60px;line-height: 60px;float: left;text-align: center;}
	.oTab .red{color: #EF6968;}
	.oTab a{color: #0C0D0F;text-decoration: none;}
	.oTab .gaer a{color: #FF6767;}
	.oTab .gaer{border-bottom:2px solid #FF6767;}
</style>


