<style type="text/css">
	.roomWrap .swiper-slide{margin:0 0.1rem;}
	.roomWrap .swiper-slide img{height:4rem;}
	.roomWrap .roomPhoto{width: 100%;height: 1.6rem;background: url(../../assets/images/ic_rec_group_banner_6.png);background-size:cover;position: relative; }
	.roomPhoto span{position: absolute;left: 0.4rem;top: 0.5rem;color: #fff;font-size: 0.4rem;} 
